import React from 'react'
import Fstyle from './Firstdesign.module.css'

function Firstdesign() {
    return (
        <div>
            <div className={Fstyle.shipping}><p>Free Shipping</p></div>
            <div className={Fstyle.scrolling}><a href="">^</a></div>
            <div className={Fstyle.full_content}>
                <div className={Fstyle.navigationn_bar}>
                    <h1 className={Fstyle.navigation_brand}>Motor<span>pro gear</span></h1>
                    <ul>
                        <li><a href="#">Home</a></li>
                        <li><a href="#product_brands">Brands</a></li>
                        <li><a href="#accessories">Products</a></li>
                        <li> <a href="#">Support</a></li>
                        <li> <a href="#">Track Order</a></li>
                        <li> <a href="#">Discount Sale</a></li>
                    </ul>
                </div>
                <div className={Fstyle.welcome}>
                    <a href="#">Start Purchasing</a>
                </div>
            </div>
            <div className={Fstyle.feedback}>
                <p>
                    Motor ProGear, popularly known as SPG is the leading manufacturer, importer and distributor of all categories of riding gear for bikers in India. Our Mission - Making safety affordable for all bikers without any compromise on quality. Being a company started and run by bikers, our products are meticulously designed with emphasis on Practicality, Functionality and Style. Our product range includes Motorcycle Helmets, Riding Jackets, Riding Pants, Riding Gloves, Motorcycle Boots and other accessories for bikers. We believe in ATGATT....... All The Gear All The Time.
                </p>
                <form>
                    <h2>Submit your feedback</h2>
                    <label>
                        <input type="text" name="name" placeholder="Name"></input><br></br>
                    </label>
                    <label>
                        <input type="email" name="mail" placeholder="Email"></input><br></br>
                    </label>
                    <label>
                        <textarea type="text" name="comment" placeholder="Comments"></textarea><br></br>
                    </label>
                    <label>
                        <input type="submit"></input>
                    </label>
                </form>
            </div>
            <div className={Fstyle.brands} id={Fstyle.product_brands}>
                <h1>Brands</h1>
                <div className={Fstyle.images}>
                    <img src="https://aspidagroup.com/wp-content/uploads/2020/05/Aspida-logo-_Artboard-1-1-1.png" className={Fstyle.bike}></img>
                    <img src="https://lezebre.lu/images/detailed/18/30960-MT_Helmets.png" className={Fstyle.bike}></img>
                    <img src="https://seeklogo.net/wp-content/uploads/2013/03/hjc-vector-logo-400x400.png" className={Fstyle.bike}></img>
                    <img src="https://axxis-helmets.com/wp-content/uploads/axxis-logo-banner-1-768x310.png" className={Fstyle.bike}></img>

                    <img src="https://motorushstore.com/wp-content/uploads/2020/02/motorush-brand-logos-scimitar.png" className={Fstyle.bike}></img>
                    <img src="https://i.pinimg.com/originals/94/05/78/940578e6f3c3121e9a9522c36a2f0bdd.png" className={Fstyle.bike}></img>
                    <img src="https://my-test-11.slatic.net/p/971c754727eeba097ac97f68845c9367.png" className={Fstyle.bike}></img>
                    <img src="https://probikerhelmets.com/wp-content/uploads/2020/09/ryo-logo.png" className={Fstyle.bike}></img>
                    <img src="https://logodix.com/logo/1621340.png" className={Fstyle.bike}></img>
                    <img src="https://logoeps.com/wp-content/uploads/2013/05/shoei-vector-logo.png" className={Fstyle.bike}></img>
                    <img src="https://powersportsoutlet.com/image/cache/catalog/brands/ls2-logo-225x225.png" className={Fstyle.bike}></img>
                    <img src="https://pinlock.com/wp-content/uploads/2020/01/V-helmets-logo.png" className={Fstyle.bike}></img>
                </div>
            </div>

            <div className={Fstyle.our_products} id={Fstyle.accessories}>
                <h1>Our Products</h1>
                <div className={Fstyle.product_images}>
                    <img src="https://i.pinimg.com/originals/97/23/84/9723843677afe05e79ff5f6b3ef3753c.jpg" className={Fstyle.products}></img>
                    <img src="https://rukminim1.flixcart.com/image/416/416/jpu324w0/rider-protective-jacket/b/e/h/3xl-bbg-06-biking-brotherhood-original-imafbzf4u4yfw8rz.jpeg?q=70" className={Fstyle.products}></img>
                    <img src="https://www.trialsbike.com.au/wp-content/uploads/2018/11/JI19PATR-1015_1-1.jpg" className={Fstyle.products}></img>
                    <img src="https://images-na.ssl-images-amazon.com/images/I/71ubzWjjjyL._AC_SL1500_.jpg" className={Fstyle.products}></img>
                    <img src="https://bikegear.in/image/cache/catalog/Forma-boots/forma-adventure-black-boots-1-600x600.jpg" className={Fstyle.products}></img>
                    <img src="http://cdn.shopify.com/s/files/1/0010/0836/9711/products/GT2-black.png?v=1598477868" className={Fstyle.products}></img>
                    <img src="https://bdlamotorbikes.co.uk/wp-content/uploads/2021/05/MT-V-14-Motorcycle-Helmet-Visor-Dark-1.jpg" className={Fstyle.products}></img>
                </div>
            </div>

            <div className={Fstyle.footer}>
                <p className={Fstyle.news_letter}>sign up for our news letter</p><br></br>
                <p className={Fstyle.subscribe}>Subscribe to know about our product launches, features, etc.</p><br></br>
                <div className={Fstyle.social_media}>
                    <a href="">Facebook</a>
                    <a href="">Twitter</a>
                    <a href="">Instagram</a>
                    <a href="">Whatsapp</a>
                </div><br></br>
                <div className={Fstyle.contacts}>
                    <a href="">Contact Us</a>
                    <a href="">About Us</a>
                </div>
            </div>
        </div>
    );
}

export default Firstdesign;